import { DEFAULT_HOME_STATE } from '../types/home';
import { DEFAULT_USER_ID, getActiveUser, getUserScopedStorageKey } from './userIdentity';
const SYNC_DEBOUNCE_MS = 1000;
let activityData = {};
let homeState = { ...DEFAULT_HOME_STATE, goals: { ...DEFAULT_HOME_STATE.goals } };
let syncStatus = {
    isSyncing: false,
    lastSyncedAt: null,
};
let activeUserId = DEFAULT_USER_ID;
let initialized = false;
let syncTimeout;
let syncPending = false;
const listeners = new Set();
const cloneActivityData = (data) => JSON.parse(JSON.stringify(data ?? {}));
const cloneHomeState = (state) => ({
    currentStreak: state.currentStreak,
    lastProcessedDate: state.lastProcessedDate,
    goals: { ...state.goals },
});
const notifyListeners = () => {
    listeners.forEach((listener) => {
        try {
            listener({ ...syncStatus });
        }
        catch (error) {
            console.error('Error notifying sync listener:', error);
        }
    });
};
const readLocalJson = (key, fallback) => {
    try {
        const raw = window.localStorage.getItem(getUserScopedStorageKey(key));
        if (!raw) {
            return fallback;
        }
        return JSON.parse(raw);
    }
    catch {
        return fallback;
    }
};
const writeLocalJson = (key, value) => {
    try {
        window.localStorage.setItem(getUserScopedStorageKey(key), JSON.stringify(value));
    }
    catch {
        //  (,  )
    }
};
const getApiBase = () => {
    const envBase = (process.env.MINIAPP_API_BASE ?? '').trim();
    const runtimeBase = typeof window !== 'undefined' && window.__MAX_API_BASE__;
    const base = envBase || runtimeBase || (typeof window !== 'undefined' ? window.location.origin : '');
    return base.replace(/\/+$/, '');
};
const buildEndpoint = (path) => {
    const base = getApiBase();
    return `${base}${path}`;
};
const updateSyncStatus = (patch) => {
    syncStatus = { ...syncStatus, ...patch };
    notifyListeners();
};
const performSync = async () => {
    if (activeUserId === DEFAULT_USER_ID) {
        return;
    }
    if (syncStatus.isSyncing) {
        syncPending = true;
        return;
    }
    updateSyncStatus({ isSyncing: true, error: undefined });
    try {
        const response = await fetch(buildEndpoint(`/api/user/${encodeURIComponent(activeUserId)}/state`), {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            credentials: 'include',
            body: JSON.stringify({
                activityData,
                homeState,
            }),
        });
        if (!response.ok) {
            throw new Error(`Sync failed with status ${response.status}`);
        }
        const payload = (await response.json());
        if (payload.activityData) {
            activityData = cloneActivityData(payload.activityData);
            writeLocalJson('activity_data', activityData);
        }
        if (payload.homeState) {
            homeState = cloneHomeState(payload.homeState);
            writeLocalJson('home_state', homeState);
        }
        updateSyncStatus({
            isSyncing: false,
            lastSyncedAt: payload.updatedAt ?? new Date().toISOString(),
            error: undefined,
        });
    }
    catch (error) {
        console.error('Failed to sync user state:', error);
        updateSyncStatus({
            isSyncing: false,
            error: error instanceof Error ? error.message : 'Unknown sync error',
        });
    }
    finally {
        syncStatus.isSyncing = false;
        if (syncPending) {
            syncPending = false;
            scheduleSync();
        }
    }
};
const scheduleSync = () => {
    if (activeUserId === DEFAULT_USER_ID) {
        return;
    }
    if (syncTimeout) {
        window.clearTimeout(syncTimeout);
    }
    syncTimeout = window.setTimeout(() => {
        syncTimeout = undefined;
        void performSync();
    }, SYNC_DEBOUNCE_MS);
};
export const initializeUserStateSync = async () => {
    if (initialized) {
        return;
    }
    const user = getActiveUser();
    activeUserId = user.userId ?? DEFAULT_USER_ID;
    activityData = cloneActivityData(readLocalJson('activity_data', {}));
    homeState = cloneHomeState(readLocalJson('home_state', { ...DEFAULT_HOME_STATE, goals: { ...DEFAULT_HOME_STATE.goals } }));
    if (activeUserId === DEFAULT_USER_ID) {
        initialized = true;
        return;
    }
    try {
        const response = await fetch(buildEndpoint(`/api/user/${encodeURIComponent(activeUserId)}/state`), {
            method: 'GET',
            credentials: 'include',
        });
        if (response.ok) {
            const payload = (await response.json());
            if (payload.activityData) {
                activityData = cloneActivityData(payload.activityData);
                writeLocalJson('activity_data', activityData);
            }
            if (payload.homeState) {
                homeState = cloneHomeState(payload.homeState);
                writeLocalJson('home_state', homeState);
            }
            updateSyncStatus({
                isSyncing: false,
                lastSyncedAt: payload.updatedAt ?? null,
                error: undefined,
            });
        }
        else {
            console.warn(`Failed to load remote state: ${response.status}`);
        }
    }
    catch (error) {
        console.warn('Unable to load remote user state, using local cache:', error);
        updateSyncStatus({
            isSyncing: false,
            error: error instanceof Error ? error.message : 'Failed to load remote state',
        });
    }
    finally {
        initialized = true;
    }
};
export const getActivityState = () => cloneActivityData(activityData);
export const setActivityState = (data) => {
    activityData = cloneActivityData(data);
    writeLocalJson('activity_data', activityData);
    scheduleSync();
};
export const getHomeState = () => cloneHomeState(homeState);
export const setHomeState = (state) => {
    homeState = cloneHomeState(state);
    writeLocalJson('home_state', homeState);
    scheduleSync();
};
export const subscribeToSyncStatus = (listener) => {
    listeners.add(listener);
    listener({ ...syncStatus });
    return () => {
        listeners.delete(listener);
    };
};
export const getSyncStatus = () => ({ ...syncStatus });
export const forceSyncNow = async () => {
    if (activeUserId === DEFAULT_USER_ID) {
        return;
    }
    if (syncTimeout) {
        window.clearTimeout(syncTimeout);
        syncTimeout = undefined;
    }
    await performSync();
};
